@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.header{background-color:#29c7ac;box-shadow:0 1px 2px rgba(0,0,0,0.15);height:73px;width:100%}.header img{width:45x;height:45px;align-items:center;margin:auto}.header nav{height:100%;max-width:992px;align-items:center;margin:auto;padding:1rem}.header nav h2{color:black;position:relative;top:-12px;left:10px;font-size:30px;letter-spacing:1px}

.tasks{background-color:white;min-height:calc(100vh - 73px);width:calc(922px - 266px);padding:74px 20px 0}.tasks .add{cursor:pointer}.tasks .add .plus{color:#29c7ac;font-size:18px}.tasks .add .addtask{color:#545454;font-size:14px;margin-left:15px}.tasks .dialog{margin-top:10px}.tasks .dialog input{width:100%;border:1px solid #ddd;background-color:white;border-radius:3px;height:35px;padding-left:10px;padding-right:10px}.tasks .dialog .actions{margin-top:10px;display:flex;justify-content:space-between}.tasks .dialog .actions .addbtn{background-color:#29c7ac;cursor:pointer;color:white;border:1px solid transparent;margin-right:5px;font-weight:bold;font-size:13px;padding:6px 12px 7px 12px;border-radius:3px !important}.tasks .dialog .actions .addbtn:disabled{background-color:gray}.tasks .dialog .actions .addbtn:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.tasks .dialog .actions .cancelbtn{all:unset;color:#555;cursor:pointer;font-size:14px;margin:0 5px}.tasks .taskItemContainer{margin-top:40px;padding:0 20px}.tasks .taskItemContainer .task-item:not(:last-child){border-bottom:1px solid #c22085}.tasks .taskItemContainer .task-item .tasker{display:flex;justify-content:space-between}.tasks .taskItemContainer .task-item .editor{display:flex;margin-bottom:7px}.tasks .taskItemContainer .task-item .editor input{width:50%;border:1px solid #ddd;background-color:white;border-radius:3px;height:35px;padding-left:10px;padding-right:10px}.tasks .taskItemContainer .task-item .editor button{background-color:#29c7ac;cursor:pointer;color:white;border:1px solid transparent;margin-left:5px;font-weight:bold;font-size:13px;padding:6px 12px 7px 12px;border-radius:3px !important}.tasks .taskItemContainer .task-item .editor button:disabled{background-color:gray}.tasks .taskItemContainer .task-item .editor button:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.tasks .taskItemContainer em{cursor:pointer;margin-left:5px;top:3px;position:relative}

.sidebar{-webkit-user-select:none;-ms-user-select:none;user-select:none;width:266px;min-height:calc(100vh - 73px);padding-top:74px;border-left:1px solid #29c7ac;background-color:#fafafa}.sidebar div{margin-bottom:10px;cursor:pointer;padding:15px}.sidebar div.active{background-color:#29c7ac}.sidebar div .icon{margin-right:10px}

.content{display:flex;max-width:992px;margin:auto;background-color:#fafafa;height:100%}

html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body{margin:0;padding:0;line-height:1;font-family:"Roboto", sans-serif;color:black;background-color:#fafafa}

